import { Component, OnInit,ElementRef,Renderer} from '@angular/core';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { DialogService } from "ng2-bootstrap-modal";
import { WithPagingService } from './with-paging.service';
import { AlertComponent } from './alert.component';
import { ConfirmComponent } from './confirm.component';

@Component({
  selector: 'app-paging',
  templateUrl: 'with-paging.component.html',
  providers: [WithPagingService]
})
export class WithPagingComponent implements OnInit {
  arrayData = [];
  html = '';
  sotrang = 0;
  page = 0;
  last = 0;
  start = 0;
  end = 0;
  list_class = '';
  class = '';
  limit = 10;
  numbers=[];
  isLoading = false;
  htmlPlayYouTube='';
  dangerousUrl='';
  trustedUrl:any;
  constructor(private withPagingService: WithPagingService, private router: Router,private elRef:ElementRef,private renderer:Renderer,private dialogService:DialogService,private sanitizer: DomSanitizer) { }
  ngOnInit(): void {
    //console.log(this.numbers);
    this.isLoading=true;
    let navigationExtras: NavigationExtras = {
      queryParams: { 'page': 1 },
      //fragment: 'anchor'
    };
    //this.router.navigate(['/login'],navigationExtras);
    this.withPagingService.getData(0)
      .then(temp => {
        //console.log(temp.data);
        this.arrayData = temp.data;
        this.sotrang = temp.sotrang;
        this.page = temp.page;
        // for (var i = 0; i < this.sotrang; i++) {
        //     this.html += `<a class="page-num" href="#">`+i+`</a>`;
        // }
        this.last = Math.ceil(temp.total / temp.limit);

        this.start = ((this.page - 7) > 0) ? this.page - 7 : 1;
        //alert(this.start)
        this.end = ((this.page + 7) < this.last) ? this.page + 7 : this.last;
        this.numbers = Array(this.end).fill(this.start).map((x,i)=>i+1);
        this.isLoading=false;
      })
      .catch(err => {
        console.log('error ' + err);
      });
  }

  showMore(a){
    alert('test');
  }

  youTubePlay(data){
    this.dialogService.addDialog(AlertComponent, {title:data.name, message:data.youtube_id});
  }

  delete(data){
    let disposable = this.dialogService.addDialog(ConfirmComponent, {
      title:'Thng bo', 
      message:'Bn c mun xa '+data.name
    })
      .subscribe((isConfirmed)=>{
          //We get dialog result
          if(isConfirmed) {
            this.withPagingService.deleteData(data.id).then(v=>{
              //console.log(v);
              //this.router.navigate(['/basic/with-paging']);
              window.location.reload();
            }).catch(err=>{
              console.log("Error "+ err);
            });
          }
          else {
              //alert('declined');
          }
      });
  //We can close dialog calling disposable.unsubscribe();
  //If dialog was not closed manually close it by timeout
  setTimeout(()=>{
      disposable.unsubscribe();
  },10000);
    
  }

}


