import { Component, OnInit, ElementRef, Renderer } from '@angular/core';
import { Http,Headers } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { WithAddService } from './with-add.service';
import { Select2Data } from "select2-component/angular";

@Component({
    selector: 'app-paging-add',
    templateUrl: 'with-paging-add.component.html',
    providers: [WithAddService]

})
export class WithPagingAddComponent implements OnInit {
    arrCateList = [];
    dataCate: Select2Data = [
        // { value: "AK", label: "Alaska" },
        // { value: "HI", label: "Hawaii", disabled: true },
        // { value: "CA", label: "California" }
    ];
    valueCate: '';
    imageUpload: any;
    submitValue: any;
    constructor(private withAddService: WithAddService,private http: Http,private router: Router) { }
    ngOnInit(): void {
        
        this.withAddService.getCateList().then(v => {
            for (let cate of v.cateList) {
                this.dataCate.push({ value: cate.id, label: cate.name })
            }
        }).catch(err => {
            console.log('Error ' + err);
        });
    }

    updateCate(value) {
        this.valueCate = value;
    }

    onSubmit(event) {
        console.log(event.value);
        console.log(this.imageUpload);
        this.submitValue = event.value;
        //const url2 = 'http://localhost/scoregame/api/v1/gamespotListAdd/add';
        const url2 = 'https://scoregame.000webhostapp.com/scoregame/api/v1/gamespotListAdd/add';
        
        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        //headers.append('Access-Control-Allow-Origin','*');  
        //headers.append('Access-Control-Allow-Methods', "GET, POST, PATCH, PUT, DELETE, OPTIONS");
        //headers.append('Access-Control-Allow-Headers', "Origin, Content-Type, X-Auth-Token");      
        // const body = JSON.stringify({ 
        //     name: this.submitValue.name,
        //     score:this.submitValue.score,
        //     score_word:this.submitValue.score_word,
        //     img:this.imageUpload,
        //     youtube_link:this.submitValue.youtube_id
        // });
        let formData = new FormData();
        formData.append('cate_id',this.valueCate);
        formData.append('name',this.submitValue.name);
        formData.append('score',this.submitValue.score);
        formData.append('score_word',this.submitValue.score_word);
        formData.append('img',this.imageUpload);
        formData.append('youtube_link',this.submitValue.youtube_id);
        return this.http.post(url2, formData)
            .toPromise()
            .then(res => res.json())
            .then(resJson => {
                //alert(resJson.info);
                if(resJson.info=='success'){
                    this.router.navigate(['/basic/with-paging']);
                }
            });
    }

    uploadImg(event) {
        let elem = event.target;
        //console.log(event.target.files);
        this.imageUpload = elem.files[0];
        // if (elem.files.length > 0) {
        //     console.log(elem.files[0]);
        //     let formData = new FormData();
        //     formData.append('fileUploadDemo', elem.files[0]);
        //     const url = 'http://localhost/scoregame/api/v1/uploadImageDemo';
        //     const headers = new Headers();
        //     headers.append('Content-Type', 'application/json');
        //     //headers.append('Access-Control-Allow-Origin','*');  
        //     //headers.append('Access-Control-Allow-Methods', "GET, POST, PATCH, PUT, DELETE, OPTIONS");
        //     //headers.append('Access-Control-Allow-Headers', "Origin, Content-Type, X-Auth-Token");      
        //     const body = JSON.stringify({ name: 'hi p trai' });
        //     return this.http.post(url, formData)
        //         .toPromise()
        //         .then(res => res.text())
        //         .then(resJson => console.log(resJson));
        // }

    }


}


