import { Component ,OnInit} from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';

export interface AlertModel {
  title: string;
  message: string;
}

@Component({
  selector: 'alert',
  template: `<div class="modal-dialog">
                <div class="modal-content">
                   <div class="modal-header">
                     <button type="button" class="close" (click)="close()" >&times;</button>
                     <h4 class="modal-title">{{title || 'Alert!'}}</h4>
                   </div>
                   <div class="modal-body">
                   <iframe allowfullscreen="allowfullscreen" width="420" height="345" [src]="trustedUrl"></iframe>
                   </div>
                   <div class="modal-footer">
                     <button type="button" class="btn btn-primary" (click)="close()">Close</button>
                   </div>
                </div>
             </div>`
})
export class AlertComponent extends DialogComponent<AlertModel, null> implements AlertModel,OnInit {
  
  title: string;
  message: string;
  dangerousUrl='';
  trustedUrl:any;
  constructor(dialogService: DialogService,private sanitizer: DomSanitizer) {
    super(dialogService);
   
  }

  ngOnInit(): void {
    this.dangerousUrl = 'https://www.youtube.com/embed/'+this.message;
    this.trustedUrl = this.sanitizer.bypassSecurityTrustResourceUrl(this.dangerousUrl);
  }
}


