import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'with-ajax',
  templateUrl: 'with-ajax.component.html'
})
export class WithAjaxComponent implements OnInit {
  ngOnInit(): void {
    $('#dtTable').DataTable({
        searching: false,
        "pagingType": "full_numbers",
        "processing": true,
        "serverSide": true,
        "columns" : [ {
            // "width" : "90%",
            "orderable" : true,
            className : "t_col",
            "name" : 'id'
         }, {
            // "width" : "5%",
            "orderable" : true,
            className : "s_col",
            "name" : 'name'
         }, {
            // "width" : "5%",
            "orderable" : false,
            className : "d_col",
            "name" : 'score'
         }],
         "ajax": {
            "url": 'http://localhost/scoregame/api/v1/gamespotListAngular',
            "data": function ( d ) {
                return $.extend( {}, d, {
                  "extra_search": 'test',
                  "extra_search 1": 'test'
                } );
              }
            
          },
        
    });
  }
}


