import { Injectable } from '@angular/core';
import { Http,Headers } from '@angular/http';
import 'rxjs/add/operator/toPromise';

@Injectable()

export class WithPagingService {
    constructor(private http: Http) { }
    getData(page1) {
        //const url='http://localhost/scoregame/api/v1/gamespotListPagingAngular?page='+page1;
        const url ='https://scoregame.000webhostapp.com/scoregame/api/v1/gamespotListPagingAngular?page='+page1;
        //const url ='http://angular4.byethost32.com/scoregame/api/v1/gamespotListPagingAngular?page='+page1;
        //const url='http://localhost/scoregame/api/testApi';
        const headers = new Headers();
        headers.append('Content-type','application/json');
        headers.append('Access-Control-Allow-Origin','*');  
        //headers.append('Access-Control-Allow-Methods', "GET, POST, PATCH, PUT, DELETE, OPTIONS");
        //headers.append('Access-Control-Allow-Headers', "Origin, Content-Type, X-Auth-Token");      
        return this.http.get(url)
            .toPromise()
            .then(res => res.json())
            .then(resJson => resJson);
    }

    deleteData(id) {
        //const url='http://localhost/scoregame/api/v1/gamespotListDelete/'+id;
        const url='https://scoregame.000webhostapp.com/scoregame/api/v1/gamespotListDelete/'+id;
        const headers = new Headers();
        headers.append('Content-type','application/json');
        // headers.append('Access-Control-Allow-Origin','*');  
        // headers.append('Access-Control-Allow-Methods', "GET, POST, PATCH, PUT, DELETE, OPTIONS");
        // headers.append('Access-Control-Allow-Headers', "Origin, Content-Type, X-Auth-Token");      
        return this.http.get(url)
            .toPromise()
            .then(res => res.json())
            .then(resJson => resJson);
    }
}


