import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-struct',
  templateUrl: './struct.component.html',
  styleUrls: ['./struct.component.css']
})
export class StructComponent implements OnInit {
  txtEnglish = '';
  txtVietNam = '';
  isShowForm = false;
  filterStatus='XEM_TAT_CA';
  arrLang = [
    { id: 1, en: 'action', vn: 'hnh ng', memorized: true },
    { id: 2, en: 'actor', vn: 'din vin', memorized: false },
    { id: 3, en: 'activity', vn: 'hot ng', memorized: true },
    { id: 4, en: 'active', vn: 'ch ng', memorized: true },
    { id: 5, en: 'bath', vn: 'tm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phng ng', memorized: true }
  ];
  isToggle = true;
  arrSubjects = ['php', 'nodejs', 'reactjs']
  constructor() { }

  ngOnInit() {
  }

  addLang() {
    this.arrLang.unshift({
      id: this.arrLang.length + 1,
      en: this.txtEnglish,
      vn: this.txtVietNam,
      memorized: false
    })
    this.txtEnglish = '';
    this.txtVietNam = '';
    this.isShowForm = false;
  }

  removeLang(id: number) {
    const index = this.arrLang.findIndex(word => word.id == id);
    this.arrLang.splice(index,1);
  }

  getShowStatus(mem:boolean){
    const dkXemTatCa = this.filterStatus=='XEM_TAT_CA';
    const dkXemDaNho= this.filterStatus=='XEM_DA_NHO' && mem;
    const dkXemChuaNho = this.filterStatus=='XEM_CHUA_NHO' && !mem;
    return dkXemTatCa||dkXemDaNho||dkXemChuaNho;

  }


}



