import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { ZeroConfigComponent } from './basic/zero-config.component';
import { WithOptionsComponent } from './basic/with-options.component';
import { WithAjaxComponent } from './basic/with-ajax.component';
import { WithPagingComponent } from './basic/with-paging.component';
import { WithPagingDetailComponent } from './basic/with-paging-detail.component';
import { WithPagingAddComponent } from './basic/with-paging-add.component';
import { WithPagingEditComponent } from './basic/with-paging-edit.component';


const routes: Routes = [
    {
        path: 'basic/zero-config',
        component: ZeroConfigComponent
    },
    {
        path: 'basic/with-options',
        component: WithOptionsComponent
    },
    {
        path: 'basic/with-ajax',
        component: WithAjaxComponent
    },
    {
        path: 'basic/with-paging',
        component: WithPagingComponent
    },
    {
        path: 'basic/with-paging/:limit/:page',
        component: WithPagingDetailComponent
    },
    {
        path: 'basic/with-paging-add',
        component: WithPagingAddComponent
    },
    {
        path: 'basic/with-paging-edit/:id',
        component: WithPagingEditComponent
    },
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { useHash: true })],
    exports: [RouterModule]
})
export class AppRoutingModule { }


