import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { Routes,RouterModule } from '@angular/router';

import { DataTablesModule } from 'angular-datatables';

import { AppRoutingModule } from './app.routing';
//import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import { BootstrapModalModule } from 'ng2-bootstrap-modal';
import { CommonModule } from "@angular/common";
const routesConfig:Routes=[
  {path:'contacts/:id',component:ContactsComponent},
  {path:'contacts',component:ContactsComponent},
  {path:'detail/:id',component:ContactDetailComponent},
  {path:'',redirectTo:'/basic/zero-config',pathMatch:'full'},
  {path:'**',component:PageNotFoundComponent}
];



import { AppComponent } from './app.component';
import { WordComponent } from './word/word.component';
import { UserFormComponent } from './user-form/user-form.component';
import { StructComponent } from './struct/struct.component';
import { PersonComponent } from './person/person.component';
import { ListPersonComponent } from './list-person/list-person.component';
import { ParentComponent } from './parent.component';
import { ChildComponent } from './child.component';
import { CardComponent } from './card.component';
import { RoundPipe } from './round.pipe';
import { IpComponent } from './ip.component';
import { WeatherComponent } from './weather/weather.component'
import {SignInComponent} from './sign-in.component';
import { ContactsComponent } from './contacts/contacts.component';
import { ContactDetailComponent } from './contact-detail/contact-detail.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component'

import { ZeroConfigComponent } from './basic/zero-config.component';
import { WithOptionsComponent } from './basic/with-options.component';
import { WithAjaxComponent } from './basic/with-ajax.component';
import { WithPagingComponent } from './basic/with-paging.component';
import { WithPagingDetailComponent } from './basic/with-paging-detail.component';
import { WithPagingAddComponent } from './basic/with-paging-add.component';
import { WithPagingEditComponent } from './basic/with-paging-edit.component';

import { Select2Module,Select2Data } from "select2-component/angular";
//import { NgbdModalBasic } from './basic/modal-basic';
import { ConfirmComponent } from './basic/confirm.component';
import { AlertComponent } from './basic/alert.component';

@NgModule({
  declarations: [
    AppComponent,
    WordComponent,
    UserFormComponent,
    StructComponent,
    PersonComponent,
    ListPersonComponent,
    ParentComponent,
    ChildComponent,
    CardComponent,
    RoundPipe,
    IpComponent,
    WeatherComponent,
    SignInComponent,
    ContactsComponent,
    ContactDetailComponent,
    PageNotFoundComponent,
    ZeroConfigComponent,
    WithOptionsComponent,
    WithAjaxComponent,
    WithPagingComponent,
    WithPagingDetailComponent,
    WithPagingAddComponent,
    ConfirmComponent,
    AlertComponent,
    WithPagingEditComponent
    //NgbdModalBasic
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    Select2Module,
    AppRoutingModule,
    CommonModule,
    BrowserModule,
    BootstrapModalModule,
    //NgbModule.forRoot(),
    RouterModule.forRoot(routesConfig),
    DataTablesModule
    
  ],
  entryComponents: [
    ConfirmComponent,
    AlertComponent
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



