import { Component, OnInit } from '@angular/core';
import { Http,Headers } from '@angular/http';
import 'rxjs/add/operator/toPromise';

@Component({
    selector: 'app-sing-in',
    template: `
        <form (submit)="onSubmit(formSignIn);" #formSignIn="ngForm">
            <input 
            placeholder="email" 
            ngModel 
            #txtEmail="ngModel"
            name="email" 
            required
            email
            />
            <p *ngIf="txtEmail.touched && txtEmail.errors?.required">
                Email is required
            </p>
            <p *ngIf="txtEmail.touched && txtEmail.errors?.email">
                Email is not valid
            </p>
            <br/><br/>
            <input type="password" placeholder="password" ngModel name="password"
            #txtPassword="ngModel"
            minlength="6"
            pattern="[a-z]*"
            />
            <p *ngIf="txtPassword.touched && txtPassword.errors?.minlength!=null">
                Password has 6 symbol
            </p>
            <p *ngIf="txtPassword.touched && txtPassword.errors?.pattern!=null">
                Password has a - z
            </p>
            <br/><br/>
            <div ngModelGroup="subjects">
                <label><input type="checkbox" [ngModel]="false" name="nodejs"/>Nodejs</label>
                <label><input type="checkbox" [ngModel]="false" name="angularjs"/>Angularjs</label>
                <label><input type="checkbox" [ngModel]="false"  name="php"/>Php</label>
            </div>
            <br/><br/>
            <button [disabled]="formSignIn.invalid">Login </button>
            <button (click)="postToScoreGame();">Post</button>
        </form>
        <p>{{txtEmail.errors | json }}</p>
        <p>{{txtPassword.errors | json }}</p>
        <p>{{formSignIn.value | json }}</p>
    `
})
export class SignInComponent {
    email = '';
    password = '';
    constructor(private http: Http) { }    
    onSubmit(formSignIn) {
        console.log(formSignIn);
    }

    postToScoreGame(){
        const url2='http://localhost/scoregame/api/v1/gamespotListAdd/add';
        const headers = new Headers();
        headers.append('Content-Type','application/json');
        //headers.append('Access-Control-Allow-Origin','*');  
        //headers.append('Access-Control-Allow-Methods', "GET, POST, PATCH, PUT, DELETE, OPTIONS");
        //headers.append('Access-Control-Allow-Headers', "Origin, Content-Type, X-Auth-Token");      
        const body = JSON.stringify({name:'hi p trai'});
        return this.http.post(url2,body,{headers})
            .toPromise()
            .then(res => res.json())
            .then(resJson => console.log(resJson));
    }
}


