import { Component } from '@angular/core';
import { Http } from '@angular/http';
import { DialogService } from "ng2-bootstrap-modal";
import 'rxjs/add/operator/toPromise';
import { ConfirmComponent } from './confirm.component';
import { AlertComponent } from './alert.component';
@Component({
  selector: 'app-zero-config',
  templateUrl: 'zero-config.component.html'
})
export class ZeroConfigComponent {
  testInput="";
  constructor(private http:Http,private dialogService:DialogService){

  }
  testUploadImg(event){
    alert(this.testInput);
    let elem = event.target;
    //console.log(event.target.files);
    if(elem.files.length>0){
      console.log(elem.files[0]);
      let formData = new FormData();
      formData.append('fileUploadDemo',elem.files[0]);
      const url='http://localhost/scoregame/api/v1/uploadImageDemo';
      const headers = new Headers();
      headers.append('Content-Type','application/json');
      //headers.append('Access-Control-Allow-Origin','*');  
      //headers.append('Access-Control-Allow-Methods', "GET, POST, PATCH, PUT, DELETE, OPTIONS");
      //headers.append('Access-Control-Allow-Headers', "Origin, Content-Type, X-Auth-Token");      
      const body = JSON.stringify({name:'hi p trai'});
      return this.http.post(url,formData)
          .toPromise()
          .then(res => res.text())
          .then(resJson => console.log(resJson));
    }
    
  }

  showConfirm() {
    let disposable = this.dialogService.addDialog(ConfirmComponent, {
        title:'Confirm title', 
        message:'Confirm message'})
        .subscribe((isConfirmed)=>{
            //We get dialog result
            if(isConfirmed) {
                alert('accepted');
            }
            else {
                alert('declined');
            }
        });
    //We can close dialog calling disposable.unsubscribe();
    //If dialog was not closed manually close it by timeout
    setTimeout(()=>{
        disposable.unsubscribe();
    },10000);
}

showAlert() {
  this.dialogService.addDialog(AlertComponent, {title:'Alert title!', message:'Alert message!!!'});
}
}


